@import '../scss/vars';

.track {
  display: grid;
  /* grid-template-columns: repeat(4, 1fr); */
  grid-template-rows: 1fr auto;
  width: 100%;
  height: auto;
  // padding: 1rem 0 0;
  // margin: 10px 0;
  // background: $black-02;

  .bars {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    width: 100%;
    height: 50px;
    // margin-bottom: 1rem;
  }
  .controls-mask {
    width: 100%;
    height:61px;
    // overflow: hidden;
  
    .controls { 
      background: $black-02;
      border-top: 1px solid $white-01;
      border-bottom: 1px solid $white-01;
      box-sizing: border-box;
      display: grid;
      grid-template-columns:  repeat(5, 1fr) repeat(2, auto);
      height:60px;
      transition: transform .5s ease-out;
      width: 100%;
      // transform: translateY(-100%);
      &.show {
        transform: translateY(0);
      }
    }
  }
}



